@import "mixin";

.navbar {
	border-radius: 0;
	font-weight: bold;
	margin-bottom: 0;
	li:hover {
		background: lighten($black, 20%); }
	li {
		@include transition-ease(0.5s);
		text-align: center;
		a {
			color: darken($white, 20%) !important;
			@include transition-ease(0.5s); }
		a:hover {
			color: $orange !important; }
		a:hover li {
			background: $white; } }
	.nav {
		margin: 0; }

	.navbar-header {
		.navbar-brand {
			font-size: 32px;
			color: $white; }
		span {
			color: $orange; } } }


.header:before {
	content: '';
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background: url('/img/header.png')	50% 50% no-repeat;
	background-size: cover; }
.header {
	position: relative;
	// z-index: 5
	background: #000;
	// height: 713px
	padding-top: 7%;
	padding-bottom: 7%;
	color: $white;
	div, button {
		margin-top: 20px;
		margin-bottom: 20px; }
	.header-content {
		margin: 0 auto; }
	h2 {
		font-size: 48px;
		color: $white;
		font-weight: 1000;
		span {
			font-weight: normal;
			color: $orange; } }
	.header-content-text {
		font-size: 36px; }
	.text-2 {
		font-size: 16px; }

	.line {
		width: 60px;
		height: 3px;
		background: $white;
		margin-left: 0; }
	.header-form {
		background: $white;
		border-radius: 10px;
		padding: 0;
		.form-h {
			color: $black;
			font-size: 20px;
			font-weight: bold;
			text-align: center;
			background: $grey;
			margin: 0 0 20px;
			padding: 15px;
			border-radius: 10px 10px 0 0;
			span {
				color: $orange; } }
		.form-group {
			margin: 25px auto;
			input {
				margin: auto;
				border: none;
				box-shadow: none;
				border-radius: 0 !important;
				border-bottom: 1px solid $grey;
				font-size: 14px; } }
		.btn {
			color: $white;
			background: $orangeform;
			padding: 15px;
			margin: 0;
			border-radius: 0 0 10px 10px;
			border: none;
			outline: none;
			font-size: 15px;
			font-weight: bold;
			margin-top: 40px;
			@include transition-ease(0.5s); }
		.btn:hover {
			background: darken($orangeform, 20%); } } }


.social {
	box-shadow: 0 1px 3px rgba(0,0,0,.38);
	margin-bottom: 2px;
	.social-content {
 }		// height: 105px
	.social-text {
		h3 {
			font-weight: bold; }
		p {
			color: $greytext; } }
	.social-icon {
		padding-top: 2%;
		padding-bottom: 2%;
		font-size: 4em;
		@include flex;
		justify-content: space-between;
		a {
			color: $greytext; }
		i {
			@include transition-ease(0.5s); }
		:hover {
			color: darken($greytext, 30%); } } }


.feature {
	.line {
		margin: 0; }
	.features-text {
		padding-left: 4.16666665%;
		padding-right: 4.16666665%; } }
.feature-1 {
	background: $grey;
	.feature-1-content {
		// @include flex
		// justify-content: space-between
		.btn-white {
			background: $orange;
			border-radius: 5px;
			padding-top: 10px;
			padding-bottom: 10px;
			font-weight: normal;
			@include transition-ease(0.5s); }
		.btn-white:hover {
			background: darken($orange, 20%); }
		.btn-white:active {
			background: darken($orange, 30%);
			outline: none; } }
	.tab {
		height: 100px;
		background: $darkgrey;
		position: relative;
		text-align: center;
		margin: 5px;
		@include transition-ease(0.5s);
		div {
			color: $white;
			font-weight: bold; } }
	.tab:hover {
		background: $orange; }
	.tab:active,
	.tab:focus {
		background: darken($orange, 30%); }
	.tab-1 {
		border-radius: 5px 5px 0 0; }
	.tab-3 {
		border-radius: 0 0 5px 5px; }
	.feature-img {
		background: url('/img/town.png') 50% 50% no-repeat;
		float: right;
		min-height: {}
		height: 320px;
		max-height: {} } }




.feature-2 {
	.feature-img {
		background: url('/img/sect1.png') 0% 0% no-repeat;
		background-size: contain;
		height: 320px; } }



.feature-3 {
	background: $grey;
	.feature-img {
		background: url('/img/sect2.png') 0% 0% no-repeat;
		background-size: contain;
		height: 320px; }
	.features-text {
		padding-left: 15px; } }

.process {
	.site-text {
		text-align: center; }
	.process-content {
		margin: 70px auto 0; }
	.process-column {
		text-align: center;
		margin-bottom: 20px; }
	.process-column:nth-child(2) {
		.process-icon {
			padding: 25px 30px; } }
	.process-icon {
		display: inline-block;
		margin: auto;
		color: $orange;
		padding: 25px 40px;
		border: 2px solid $orange;
		border-radius: 50px;
		font-size: 32px; } }


.pricing {
	background: $grey;

	.site-text {
		text-align: center; }
	.pricing-content {
		text-align: center;
		margin: 70px auto 0; }
	.pricing-slot {
		text-decoration: none;
		.package {
			background: $darkgrey;
			color: $white;
			@include translate(0, 30px);
			@include transition-ease(0.5s);

			.package-name {
				border-bottom: 1px solid $grey;
				padding: 20px 0;
				font-size: 20px;
				font-weight: bold; }
			.package-price {
				font-family: Raleway;
				font-size: 100px;
				span {
					font-size: 35px; } }
			.package-caption {
				color: darken($grey, 10%);
				padding: 20px 0;
				font-style: italic; } }
		.recommendation {
			padding: 10px 0;
			display: block;
			background: $orange;
			text-transform: uppercase;
			color: $white;
			text-decoration: none;
			@include transition-ease(0.5s); }

		.package-list {
			li {
				list-style: none;
				padding: 10px 0;
				background: $white;
				border-bottom: 1px solid $grey;
				color: $darkgrey;
				text-transform: uppercase; } }
		.recommendation:hover {
			background: darken($orange, 20%); } } }

.pricing-slot:hover .package {
	@include translate(0, 0px); }

.testimonials {
	.site-text {
		text-align: center; }
	.coment {
		// background: url('/img/u229.png') no-repeat
		// background-size: contain
		background: $grey;
		text-align: left;
		font-style: italic;
		padding: 20px;
		font-size: 14px;
		overflow: hidden; }

	.testimonials-icon:before {
		content: '';
		width: 0;
		// height: 0
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-top: 15px solid $grey;
		position: relative;
		top: -51px; }

	.testimonials-icon {
		padding: 30px;
		margin-left: 10px;
		background: url('/img/avatar.png') no-repeat 50% 50%;
		border-radius: 40px;
		float: left; }
	.testimonials-column {
		.dib {
			.site-h, .site-text {
				margin: 5px 10px !important;
				text-align: left; } } } }


.custom {
	background: $black url('/img/img3.png') no-repeat 50% 50%;
	background-size: cover;
	.section-h {
		color: $white; }
	.line {
		background: $white !important; } }


.contact {
	.form-group {
		margin: 8px auto; }
	.contact-form {
		button {
			background: $orange;
			color: $white;
			font-weight: bold;
			padding: 10px 20px;
			margin: 20px;
			@include transition-ease(0.5s); }
		button:hover {
			background: darken($orange, 20%); } } }



footer {
	background: $black;
	padding: 20px 0;
	.footer-content {
		width: 310px;
		margin: auto;
		.footer-social {
			@include flex;
			justify-content: space-between;
			a {
				color: $grey;
				font-size: 24px;
				@include transition-ease(0.5s); }
			a:hover {
				color: darken($greytext, 20%); } }
		.footer-text {
			text-align: center;
			margin-top: 10px; } } }

